import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { InAppBrowser } from '@ionic-native/in-app-browser';

import { MyApp } from './app.component';
import { HomePageModule } from '../pages/home/home.module';
// import { ListPage } from '../pages/list/list';
import { AboutPageModule } from '../pages/about/about.module';
import { ActivitiesPageModule } from '../pages/activities/activities.module';
import { GiftPageModule } from '../pages/gift/gift.module';
import { LocationPageModule } from '../pages/location/location.module';
import { ContactPageModule } from '../pages/contact/contact.module';
import { CancellationPageModule } from '../pages/cancellation/cancellation.module';
import { BookingPageModule } from '../pages/booking/booking.module';
import { ListPageModule } from '../pages/list/list.module';
import { RegisterPageModule } from '../pages/register/register.module';

// Web Service
import { HttpModule } from '@angular/http';
import { Constant } from '../webService/constant';
import { Handler } from '../webService/handler';
import { WebService } from '../webService/webservice';

// Plugin
import { DatePicker } from '@ionic-native/date-picker';

import { DatePipe } from '@angular/common';

@NgModule({
  declarations: [MyApp],

  imports: [
    HttpModule,
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/booking/booking.module#BookingPageModule', name: 'BookingPage', segment: 'booking', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/about/about.module#AboutPageModule', name: 'AboutPage', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/activities/activities.module#ActivitiesPageModule', name: 'ActivitiesPage', segment: 'activities', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/gift/gift.module#GiftPageModule', name: 'GiftPage', segment: 'gift', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/location/location.module#LocationPageModule', name: 'LocationPage', segment: 'location', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contact/contact.module#ContactPageModule', name: 'ContactPage', segment: 'contact', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cancellation/cancellation.module#CancellationPageModule', name: 'CancellationPage', segment: 'cancellation', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module#RegisterPageModule', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/about-us/about-us.module#AboutUsPageModule', name: 'AboutUsPage', segment: 'about-us', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/payment/payment.module#PaymentPageModule', name: 'PaymentPage', segment: 'payment', priority: 'low', defaultHistory: [] }
  ]
}),
    AboutPageModule,
    ActivitiesPageModule,
    HomePageModule,
    GiftPageModule,
    CancellationPageModule,
    BookingPageModule,
    ContactPageModule,
    LocationPageModule,
    ListPageModule,
	RegisterPageModule
    
  ],

  bootstrap: [IonicApp],

  entryComponents: [MyApp],

  providers: [StatusBar,SplashScreen, InAppBrowser,{provide: ErrorHandler, useClass: IonicErrorHandler}, DatePicker, 
    Handler,DatePipe,WebService, Constant]

})
export class AppModule {}



